<template lang="pug">
    .programmes__content
        .programmes__title Учебные программы
        .programmes__filter
            .programmes__filter-item
                label.label__filter Выберите департамент:
                select(@change="onChange()" v-model="selectedDepartment").programmes__filter-select
                    option(:value='0') Все
                    option(:value='item.id' v-for='item in departments') {{item.name}}
            .programmes__filter-item
                label.label__filter Выберите автора:
                select(v-model="selectedAuthor").programmes__filter-select
                    option(:value='0') Все
                    option(:value='item.id' v-for='item in filteredTeachers') {{item.lastName}} {{item.firstName}}
            .programmes__filter-item
                label.label__filter Выберите предмет:
                select(v-model="selectedSubject").programmes__filter-select
                    option(:value='0') Все
                    option(:value='subject.id' v-for='subject in filteredSubjects') {{subject.name}}
            .programmes__filter-item
                label.label__filter(for='classes') Выберите класс:
                select(name='classes' v-model="selectedClass").programmes__filter-select
                    option(:value='0') Все
                    option(:value='item' v-for='item in 12') {{item}}
            .programmes__filter-item
                label.label__filter(for='languages') Выберите язык:
                select(v-model="selectedLanguage").programmes__filter-select
                    option(value='') Все
                    option(value='Казахский') Казахский
                    option(value='Русский') Русский
        ul.programmes__list
            li.programmes__item(v-for="programme,idx in filteredProgrammes" :key="programme.id")
                programme-item(
                    :index='idx'
                    :programme='programme'
                )
</template>
<script>
import {mapState, mapActions} from "vuex";
export default {
    components: {
        addProgrammeForm: () => import("../../teacher/programmes/add-programme-form"),
        programmeItem: () => import("../../teacher/programmes/programme-item")
    },
    created() {
        this.fetchProgrammes(),
        this.fetchDepartments(),
        this.fetchTeachers(),
        this.fetchSubjects()
    },
  data () {
    return {
        selectedDepartment: 0,
        selectedAuthor: 0,
        selectedLanguage: '',
        selectedSubject: 0,
        selectedClass: 0,
        addProgrammeFormVisible: false,
        programmePointsVisible: false
    }
  },   
  computed: {
       ...mapState ("programmes", {
            programmes: state => state.programmes
        }),
        ...mapState ("departments", {
            departments: state => state.departments
        }),
        ...mapState ("subjects", {
            subjects: state => state.subjects
        }),
        ...mapState ("teachers", {
            teachers: state => state.teachers
        }),
        filteredTeachers: function(){
            return this.teachers
            .filter(ele=>
            this.selectedDepartment == 0 || ele.department.id == this.selectedDepartment
            )
        },
        filteredSubjects: function(){
            return this.subjects
            .filter(ele=>
            this.selectedDepartment == 0 || ele.department.id == this.selectedDepartment
            )
        },
        filteredProgrammes: function() {
            return this.programmes
            //filter by department
            .filter(programme => {
                return this.selectedDepartment == 0 || programme.group.department.id == this.selectedDepartment;
            })
            //filter by author
            .filter(programme => {
                return this.selectedAuthor == 0 || programme.group.teacher.id == this.selectedAuthor;
            })
            //filter by subject
            .filter(programme => {
                return this.selectedSubject == 0 || programme.group.subject== this.selectedSubject;
            })
            //filter by class
            .filter(programme => {
                return this.selectedClass == 0 || programme.group.groupClass== this.selectedClass;
            })
            //filter by language
            .filter(programme => {
                return this.selectedLanguage == '' || programme.group.language== this.selectedLanguage;
            })
        }
    },   
  methods: {
    ...mapActions("programmes", ["fetchProgrammes"]),
    ...mapActions("departments", ["fetchDepartments"]),
    ...mapActions("subjects", ["fetchSubjects"]),
    ...mapActions("teachers", ["fetchTeachers"]),
      addProgramme() {
          this.addProgrammeFormVisible=!this.addProgrammeFormVisible
          document.getElementById('addProgramme').setAttribute("disabled", "true")
      },
      eventNewProgramme() {
          this.addProgrammeFormVisible=!this.addProgrammeFormVisible
          document.getElementById('addProgramme').removeAttribute("disabled")
      },
      onChange() {
            this.selectedSubject = 0
            this.selectedAuthor = 0
      }
  }
}
</script>
